// 
import { defineComponent, h, computed, ref } from "@vue/runtime-core";
import Circle from "./component/Circle";
import StartPage from './page/StartPage';
import GamePage from './page/GamePage';
import EndPage from './page/EndPage';

export default defineComponent({
  setup() {
    // string,numberVUE3ref
    // ref
    const currentPageName = ref("StartPage");
    console.log(currentPageName);
    // String
    // 
    const currentPage = computed(() => {
      if (currentPageName.value === "StartPage") {
        // currentPageName.value
        return StartPage;
      } else if (currentPageName.value === "GamePage") {
        return GamePage;
      }else if (currentPageName.value === "EndPage") {
        return EndPage;
      }
    });
    return {
      currentPage,
      currentPageName,
    }
  },
  render(ctx) {
    // vnode
    // const vnode = h('rect', { x: 100, y: 100 }, ["....", h('circle', {x: 150,y: 150}),h(Circle)]);
    // console.log(vnode);
    // return vnode;
    return h("Container", [h(ctx.currentPage, {
      onChangePage(page){
        console.log(page)
        ctx.currentPageName = page; //.vuerender
      }
    })]);
    // return h("Container", [h(GamePage)]);
  }
});