import { defineComponent, h } from "@vue/runtime-core";
import endPageImg from "../../assets/end_page.jpg";
import reStartBtnImg from "../../assets/re_startBtn.png";

export default defineComponent({
  setup(pops, ctx) {
    // vue3 (thisctx)
    const onClick = () => {
      ctx.emit("changePage", "GamePage");//page
    }
    return {
      //  ctx
      onClick,
    }
  },
  render(ctx) {
    return h('Container', [
      h('Sprite', { texture: endPageImg, width: 750, height: 800 }),
      h('Sprite', {
        texture: reStartBtnImg,
        x: 275,
        y: 350,
        interactive: true, //
        onClick: ctx.onClick //ctx
      })
    ]);
  }
})